

*:focus {
    outline: none;
  }
  
  .inputGroup {
    position: relative;
    margin-bottom: 30px;
  }
  
  .inputGroup label {
    position: absolute;
    top: 0;
    left: 5px;
    pointer-events: none;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    font-size: 16px;
    font-family: Trebuchet MS;
    color: white;
    font-weight: 500;
    text-transform: uppercase;
  }
  
  .inputGroup input,
  .inputGroup textarea {
    display: block;
    border: none;
    border-bottom: 1px solid grey;
    left: 25px;
    padding: 5px;
    background: none;
    color: #ccc;
  }
  
  .inputGroup input:focus ~ label,
  .inputGroup input:valid ~ label,
  .inputGroup textarea:focus ~ label,
  .inputGroup textarea:valid ~ label {
    top: -20px;
    -webkit-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    transition: all 0.3s ease;
    font-size: 13px;
  }
  
  .inputGroup input:focus ~ .inputBar:before,
  .inputGroup input:focus ~ .inputBar:after,
  .inputGroup textarea:focus ~ .inputBar:before,
  .inputGroup textarea:focus ~ .inputBar:after {
    width: 50%;
  }
  
  .inputGroup textarea {
    height: 100px;
    overflow: auto;
    resize: none;
  }
  

  
  .inputGroup .inputBar:before,
  .inputGroup .inputBar:after {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    width: 0;
    background: white;
    height: 2px;
    -webkit-transition: all 0.2s ease;
    -o-transition: all 0.2s ease;
    transition: all 0.2s ease;
  }
  
  .inputGroup .inputBar:after {
    right: 50%;
  }
  
  .inputGroup .inputBar:before {
    left: 50%;
  }
  
  .submit {
    border: none;
    width: 100px;
  background-color: transparent;
    text-transform: uppercase;
  border: 1px solid black;
      font-size: 15px;
      -webkit-transition: all ease .5s;
      -o-transition: all ease .5s;
      transition: all ease .5s;
    position: relative;
    cursor: pointer;
    font-weight: bold;
    color: white;
    height:2em;
  }
  
  .submit:hover {
    color:black;
    background-color: white;
  
  }
  
  button:active {
    top: 6px;
    -webkit-box-shadow: 0 0;
            box-shadow: 0 0;
  }
  


